<style lang="less" scoped>
  @import "../../styles/common.less";
  @import "../../styles/table.less";
  .ivu-table .select-table-info-row td{
    background-color: #dcdcdc;
    border-right:0;
  }
  .detail-title div{
    padding: 0 15px;
    display: inline-block;
    /*text-decoration: underline;*/
  }
  .ivu-form-item {
    margin-bottom:5px;
  }
</style>
<template>
  <div class="single-page-shadow">
    <Row>
      <Card shadow>
        <div slot="title" class="detail-title">
          <Icon type="ios-list-box-outline"></Icon>
            <div style="font-weight: bolder">任务详情</div>
          <div v-if="taskInfo.task_type ==='GITREPOSITORY'">项目: {{ taskInfo.app_name }}</div>
          <div v-if="taskInfo.task_type ==='GITREPOSITORY'">版本: {{ taskInfo.current_tag }}</div>
          <div v-if="taskInfo.valid ==='0' && isAdmin" style="display: inline-block;color:#d84a38;">测试版本</div>
          <span style="float:right">
            <Checkbox @on-change="saveChange" v-model="showCount">显示统计</Checkbox>
          </span>
        </div>
        <Row>
          <Col span="24" v-if="showCount">
            <Col span="12"  style="height:400px">
              <chart ref="pie1" :options="orgOptions" :autoresize="true" @click="clickPie" style="width:100%;"></chart>
            </Col>
            <Col span="12" >
              <chart ref="bar1" :options="orgOptions2" :autoresize="true" style="width:100%;height:200px"></chart>
            </Col>
            <Col span="12" >
              <chart ref="bar2" :options="orgOptions3" :autoresize="true" style="width:100%;height:200px"></chart>
            </Col>
            <Divider />
          </Col>

          <Col span="24">
          <Form inline >

            <FormItem prop="ai_result">
              <Select v-model="search.ai_result" style="width:200px" @on-change="queryData">
                <Option v-for="item in statusList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem prop="review_status">
              <Select v-model="search.review_status" style="width:200px" @on-change="queryData">
                <Option v-for="item in reviewStatusList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem>
              <Checkbox @on-change="queryData" v-if="isAdmin" v-model="reviewCheck">未审核</Checkbox>
            </FormItem>
            <FormItem>
              <Button type="primary" @click="returnList">返回列表</Button>
            </FormItem>
            <FormItem>
              <Button type="default" @click="queryData">刷新</Button>
            </FormItem>
          </Form>
          <div style="padding-bottom: 10px;">
            <Page show-total v-show="showCount" :total="totalNumber" :current="currentPage" show-elevator @on-change="pageChange" :page-size="pageSize"></Page>
          </div>
          <Table :row-class-name="getClassName" border :loading="tableLoading" :columns="columns" :data="tableData" size="small">
            <template slot-scope="{ row }" slot="ai_result">
              <Tooltip theme="light" max-width="900" :content="row.ai_recommend" placement="right">
                <div v-if="row.ai_result" style="cursor: pointer">{{row.ai_result}}</div>
                <div v-if="!row.ai_result" style="cursor: pointer">正在处理中</div>
              </Tooltip>
            </template>
            <template slot-scope="{ row }" slot="review_status">
              <div v-if="row.review_status === '1'">通过</div>
              <div v-else-if="row.review_status === '0'">不通过</div>
              <div v-else>未审核</div>
            </template>
          </Table>
          </Col>
        </Row>
        <br>
        <Page show-total :total="totalNumber" :current="currentPage" show-elevator @on-change="pageChange" :page-size="pageSize"></Page>
        <Divider />
      </Card>
    </Row>
    <Row>
      <task-sql-detail ref="detailCom" @reload="getData" :ai-result="search" :review-count="reviewCount"></task-sql-detail>
    </Row>
  </div>
</template>
<script>
  // import axios from 'axios';
  import util from '@/libs/util'
  import taskSqlDetail from './taskSqlDetail'
  import { getTaskSqlList, getTaskList } from '@/api/task';
  import { getUserInfoByToken } from '@/api/login';
  import { updateUser } from '@/api/settings'

  export default {
    name: 'TaskDetail',
    components: {
      taskSqlDetail
    },
    data () {
      return {
        orgOptions: {
          title : {
            text: '任务详细情况图解',
            // subtext: '纯属虚构',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          series : []
        },
        orgOptions2: {
          grid: {
            bottom: 30
          },
          legend: {
          },
          tooltip: {},
          dataset: {
            source: [
            ]
          },
          // title : {
          //   text: '不通过分类',
          //   x:'left'
          // },
          xAxis: {type: 'category'},
          yAxis: {
            minInterval: 1
          },
          // Declare several bar series, each will be mapped
          // to a column of dataset.source by default.
          series: [
          ]
        },
        orgOptions3: {
          grid: {
            bottom: 40
          },
          legend: {
          },
          tooltip: {},
          // title : {
          //   text: '异常分类',
          //   x:'left',
          // },
          dataset: {
            source: [
            ]
          },
          xAxis: {type: 'category'},
          yAxis: {},
          // Declare several bar series, each will be mapped
          // to a column of dataset.source by default.
          series: [
          ]
        },
        statusList: [
          {
            value: '全部',
            label: '全部',
          },
          {
            value: 'PASS',
            label: 'PASS',
          },
          {
            value: 'NOPASS',
            label: 'NOPASS',
          },
          {
            value: 'INVALID',
            label: 'INVALID',
          }
        ],
        reviewStatusList: [
          {
            value: '0',
            label: '不通过',
          },
          {
            value: '1',
            label: '通过',
          }
        ],
        columns: [
          {
            title: 'AI评审结论',
            key: 'ai_result',
            slot: 'ai_result'
          },
          {
            title: 'DBA审核结果',
            slot: 'review_status'
          },
          {
            title: '数据库类型',
            key: 'db_type'
          },
          {
            title: 'SCHEMA名称',
            key: 'schema_name'
          },
          {
            title: 'SQL明细',
            key: 'sql_text',
            width: 400,
            ellipsis: true
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h(
                    'i-button',
                    {
                      props: {
                        size: 'small',
                        type: 'text'
                      },
                      on: {
                        click: () => {
                          if (params.row.ai_result) {
                            this.$store.commit('setSelectTaskSeq', params.row.sql_sequence)
                            this.$refs.detailCom.showDetail(params.row)
                          } else {
                            this.$Message.error({content: '任务正在处理中', duration: 4})
                          }
                        }
                      }
                    },
                    '查看明细'
                )
              ]);
            }
          }
        ],
        reviewCheck: undefined,
        tableLoading: true,
        totalNumber: 0,
        currentPage: 1,
        pageSize: 15,
        tableData: [],
        search: {
          ai_result: '全部',
          review_status: '0'
        },
        showCount: false,
        taskInfo: {},
        reviewCount: 1
      };
    },
    computed: {
      isAdmin() {
        return sessionStorage.getItem('auth').indexOf('admin') >= 0
      }
    },
    methods: {
      getClassName(row, index) {
        if (row.sql_sequence === this.$store.state.taskSqlSelectedSeq) {
          return 'select-table-info-row'
        }
      },
      saveChange(v) {
        updateUser({username: sessionStorage.getItem('user'), show_count: v}).then(res => {
          this.this.getUserInfo()
        }).catch(e => {
        })
      },
      getData(select) {
        this.tableLoading = true;
        const limit = this.pageSize
        const offset = this.pageSize * (this.currentPage - 1)
        let _f = this.search.ai_result
        if (this.search.ai_result === '全部') {
          _f = null
        }
        this.$store.commit('setReviewCheck',  this.reviewCheck)
        const currSeq = this.$store.state.taskSqlSelectedSeq
        getTaskSqlList(limit, offset, this.task_id, _f, '', '', '', this.search.review_status, this.isAdmin ? this.reviewCheck: '').then(res => {
          this.tableData = res.data.data

          if (this.tableData.map(v => v.sql_sequence).indexOf(currSeq) < 0) {
            if (this.tableData.length > 0) {
              this.$store.commit('setSelectTaskSeq', this.tableData[0].sql_sequence)
              if (select) {
                this.$refs.detailCom.showDetail(this.tableData[0])
              }
            }
          }
          this.reviewCount = res.data.pallas_count
          this.totalNumber = res.data.count
          this.orgOptions.series = [
            {
              name: '类型',
              type: 'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data:[
                {value:res.data.pass, name:'通过'},
                {value:res.data.invalid, name:'非法'},
                {value:res.data.nopass, name:'不通过'}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
          const _bar = {type: 'bar'}
          this.orgOptions2.dataset= {
            source: [
              ['不通过分类'].concat(res.data.nopass_count.filter(v => v.count).map(v => v.desc)),
              ['不通过分类'].concat(res.data.nopass_count.filter(v => v.count).map(v => v.count)),
            ],
          }
          this.orgOptions2.series = res.data.nopass_count.filter(v => v.count).map(v => _bar)
          this.orgOptions3.dataset= {
            source: [
              ['异常分类'].concat(res.data.exception.filter(v => v.count).map(v => v.desc)),
              ['异常分类'].concat(res.data.exception.filter(v => v.count).map(v => v.count)),
            ]
          }
          this.orgOptions3.series= res.data.exception.filter(v => v.count).map(v => _bar)
        }).catch(e => {
          this.bus.$emit('errorHandler', e);
        }).finally(() => {
          this.tableLoading = false;
        })
      },
      pageChange (p) {
        this.currentPage = p
        this.getData()
        this.bus.$emit('taskSqlListChange', this.search.ai_result, p);
      },
      returnList() {
        util.openPage(this, 'task_list')
      },
      queryData () {
        this.pageChange(1)
      },
      getUserInfo() {
        getUserInfoByToken().then(res => {
          if (res.data.data.show_count === 1) {
            this.showCount = true
          } else {
            this.showCount = false
          }
        }).catch(e => {
          console.log(e)
        })
      },
      getTaskInfo() {
        return getTaskList(15, 0, '', '', '', this.task_id).then(res => {
          return res
        }).catch(e => {
          console.log(e)
          this.bus.$emit('errorHandler', e);
        })
      },
      clickPie(event, instance, echarts){
        if (event.data.name === '通过') {
          this.search.ai_result = 'PASS'
        } else if (event.data.name === '不通过') {
          this.search.ai_result = 'NOPASS'
        } else if (event.data.name === '非法') {
          this.search.ai_result = 'INVALID'
        }
        this.getData()
      }
    },
    async mounted () {

      if (this.isAdmin) {
        this.reviewCheck = true
        this.$store.commit('setReviewCheck',  this.reviewCheck)
      }

      this.bus.$on('taskSqlListChange', (filter, page) => {
        console.log(filter, page)
        this.currentPage = page
        this.getData()
      })

      await this.getUserInfo()
      if (this.$route.query.task_id) {
        this.task_id = this.$route.query.task_id

        const res =  await this.getTaskInfo()
        if (res.data.data.length === 1) {
          this.taskInfo = res.data.data[0]
        }
        if (this.taskInfo.task_type === 'SINGLE') {
          this.search.ai_result = '全部'
        }
        this.getData()
      } else {
        this.$Message.error({content: '未获取参数即将返回任务列表', duration: 4})
        setTimeout(() => {
          this.$router.push({
            name: 'task_list'
          })
        }, 5000)
      }
    },
    destroyed() {
      this.bus.$off('taskSqlListChange');
    }
  };
</script>
