<style lang="less">
  @import "../../styles/common.less";
  @import "../../styles/table.less";
  .ivu-table .select-table-info-row td{
    background-color: #dcdcdc;
    border-right:0;
  }
</style>
<template>
  <div>
    <Row>
      <Col span="24">
      <Page show-total simple :total="totalNumber" :show-elevator="false" :current="currentPage" @on-change="pageChange" :page-size="pageSize"></Page>
      <br>
      <Table :row-class-name="getClassName" border @on-row-click="clickRow" :loading="tableLoading" :columns="columns" :data="tableData" size="small">
        <template slot-scope="{ row }" slot="review_status">
          <div v-if="row.review_status === '1'">通过</div>
          <div v-if="row.review_status === '0'">未通过</div>
          <div v-if="row.review_status === null">未审核</div>
        </template>
      </Table>
      </Col>
    </Row>
    <!--<Page show-total :total="totalNumber" :show-elevator="false" :current="currentPage" @on-change="pageChange" :page-size="pageSize"></Page>-->
  </div>
</template>
<script>
  // import axios from 'axios';
  import util from '../../libs/util'
  import { getTaskSqlList } from '@/api/task';

  export default {
    name: 'TaskSqlDetailList',
    components: {
    },
    props: {
      search: {type: Object, default: () => {}}
    },
    data () {
      return {
        statusList: [
          {
            value: 'PASS',
            label: 'PASS',
          },
          {
            value: 'NOPASS',
            label: 'NOPASS',
          }
        ],
        columns: [
          {
            title: '审核结果',
            key: 'review_status',
            slot: 'review_status',
            width: 100,
          },
          {
            title: 'SQL明细',
            key: 'sql_text',
            ellipsis: true
          },
          // {
          //   title: '操作',
          //   key: 'action',
          //   align: 'center',
          //   width: 80,
          //   render: (h, params) => {
          //     return h('div', [
          //       h(
          //           'Button',
          //           {
          //             props: {
          //               size: 'small',
          //               type: 'text'
          //             },
          //             on: {
          //               click: () => {
          //                 this.$store.commit('setSelectTaskSeq', params.row.sql_sequence)
          //                 this.$emit('selectRow', params.row)
          //               }
          //             }
          //           },
          //           '显示'
          //       )
          //     ]);
          //   }
          // }
        ],
        tableLoading: true,
        totalNumber: 0,
        currentPage: 1,
        pageSize: 15,
        tableData: [],
        selectPrevNext: null
      };
    },
    computed: {
      isAdmin() {
        return sessionStorage.getItem('auth').indexOf('admin') >= 0
      }
    },
    methods: {
      getClassName(row, index) {
        if (row.sql_sequence === this.$store.state.taskSqlSelectedSeq) {
          return 'select-table-info-row'
        }
      },
      clickRow(row) {
        this.$store.commit('setSelectTaskSeq', row.sql_sequence)
        this.$emit('selectRow', row)
      },
      getData() {
        console.log(this.search)
        let _r = this.search.ai_result
        if (this.search.ai_result === '全部') {
          _r = null
        }
        this.tableLoading = true;
        const limit = this.pageSize
        const offset = this.pageSize * (this.currentPage - 1)
        const reviewCheck = this.$store.state.reviewCheck
        getTaskSqlList(limit, offset, this.task_id, _r, '', '', '', this.search.review_status, this.isAdmin ? reviewCheck: '').then(res => {
          this.tableData = res.data.data
          if (this.selectPrevNext === 'prev') {
            this.$store.commit('setSelectTaskSeq', this.tableData[this.tableData.length - 1].sql_sequence)
            this.$emit('selectRow', this.tableData[this.tableData.length - 1])
          }
          if (this.selectPrevNext === 'next') {
            this.$store.commit('setSelectTaskSeq', this.tableData[0].sql_sequence)
            this.$emit('selectRow', this.tableData[0])
          }
          this.bus.$emit('closePrevNextLoading', false)
          this.totalNumber = res.data.count
          this.$emit('showErrorCount', res.data)
        }).catch(e => {
        }).finally(() => {
          this.tableLoading = false;
        })
      },
      pageChange (p) {
        this.currentPage = p
        this.bus.$emit('taskSqlListChange', this.search.ai_result, p)
        this.getData()
      },
      queryData () {
        this.getData()
      },
      prevResult(sequence) {
        for (let i in this.tableData) {
          if (this.tableData[i].sql_sequence === sequence) {
            console.log(sequence)
            this.$store.commit('setSelectTaskSeq', sequence)
            if (parseInt(i) === 0) {
              if (this.currentPage === 1) {
                this.$Message.warning({content: '已经是第一条啦', duration: 5})
                this.bus.$emit('closePrevNextLoading', false)
              } else {
                this.currentPage -= 1
                this.selectPrevNext = 'prev'
                this.bus.$emit('taskSqlListChange', this.search.ai_result, this.currentPage)
              }
            } else {
              this.$store.commit('setSelectTaskSeq', this.tableData[parseInt(i) - 1].sql_sequence)
              this.$emit('selectRow', this.tableData[parseInt(i) - 1])
              this.bus.$emit('closePrevNextLoading', false)
            }
            break
          }
        }
      },
      nextResult(sequence) {
        for (let i in this.tableData) {
          if (this.tableData[i].sql_sequence === sequence) {
            console.log(sequence)
            if ((parseInt(i) + 1) === this.tableData.length) {
              // this.$store.commit('setSelectTaskSeq', this.tableData[0].sql_sequence)
              // this.$emit('selectRow', this.tableData[0])

              // if (this.totalNumber / this.pageSize)
              console.log(Math.ceil(this.totalNumber / this.pageSize))
              if (Math.ceil(this.totalNumber / this.pageSize) === this.currentPage) {
                this.$Message.warning({content: '已经是第最后一条啦', duration: 5})
                this.bus.$emit('closePrevNextLoading', false)
              } else {
                this.currentPage += 1
                this.selectPrevNext = 'next'
                this.bus.$emit('taskSqlListChange', this.search.ai_result, this.currentPage)
              }
            } else {
              this.$store.commit('setSelectTaskSeq', this.tableData[parseInt(i) + 1].sql_sequence)
              this.$emit('selectRow', this.tableData[parseInt(i) + 1])
              this.bus.$emit('closePrevNextLoading', false)
            }
            break
          }
        }
      },
    },
    mounted () {
      this.bus.$on('taskSqlListChange', (filter, page) => {
        console.log(filter, page)
        this.currentPage = page
        this.getData()
      });

      // this.currentpage();
      this.task_id = this.$route.query.task_id
      // this.getData()
    },
    destroyed() {
      this.bus.$off('taskSqlListChange');
    }
  };
</script>
