<style lang="less">
  .ai-result {
    font-weight:bold;
    font-size: 16px;
    margin: 0 6px;
  }
  div .ai-result {
    padding-bottom:3px;
    display: inline-block;
  }
  .ai-result.exception {
    color:red;
  }
  .ai-result.pass {
    color:#428bca;
  }
  .ai-result.nopass {
    color:red;
  }
  .ivu-divider-horizontal {
    margin:15px 0;
  }
  .ivu-tabs-bar {
    margin-bottom:5px !important;
  }
  .CodeMirror-merge-r-inserted, .CodeMirror-merge-l-inserted {
    background: none;
  }
</style>
<template>
  <div >
    <Modal :closable="false" v-model="show" fullscreen  :ok-text="'确定'" :cancel-text="'关闭'">
      <div slot="header" class="ivu-modal-header-inner">
        {{ `sql明细详情展示：序列：${rowInfo.sql_sequence}，Schema：${rowInfo.schema_name}，数据库类型：${rowInfo.db_type}，剩余待审核数：${reviewCount}` }}
        <div v-if="rowInfo.valid ==='0' && isAdmin" style="display: inline-block;color:#d84a38;">测试数据</div>
        <div style="display:inline-block;float:right;cursor:pointer" @click="show = false">
          <Icon type="ios-close" size="24" />
        </div>
      </div>
      <Row>
        <Col span="6">
        <div class="ai-result">评估结果:</div>
        <div class="ai-result pass" v-if="this.rowInfo.ai_result === 'PASS'">通过</div>
        <div class="ai-result nopass" v-if="this.rowInfo.ai_result === 'NOPASS'">不通过</div>
        <div class="ai-result exception" v-if="['PASS', 'NOPASS'].indexOf(this.rowInfo.ai_result) <0 ">异常</div>
        </Col>
        <Col span="6">
          <div class="ai-result">DBA审核结果:</div>
          <div class="ai-result pass" v-if="rowInfo.review_status === '1'">通过</div>
          <div class="ai-result nopass" v-if="rowInfo.review_status === '0'">未通过</div>
          <div class="ai-result" v-if="rowInfo.review_status === null">未审核</div>
        </Col>
        <Col span="12" style="text-align:right">
        <div class="ai-result" style="display: inline-block">
          <Button type="default" :loading="prevLoading" :disabled="errorCount === 0" @click="prevResult" shape="circle" icon="md-arrow-back"></Button>
        </div>
        <div class="ai-result" style="display: inline-block">
          <Button v-if="isAdmin" type="primary" :loading="updateLoading" @click="updateTaskSql('1')">通过</Button>
        </div>
        <div class="ai-result" style="display: inline-block">
          <Button v-if="isAdmin" type="warning" :loading="updateLoading" @click="updateTaskSql('0')">不通过</Button>
        </div>
        <div class="ai-result" style="display: inline-block">
          <Button type="default" @click="showList = true">任务列表 </Button>
          <!--<Button type="info" @click="showList = true">任务列表 {{solvedCount}}/{{errorCount}} </Button>-->
        </div>
        <div class="ai-result" style="display: inline-block">
          <Button v-if="isAdmin" type="success" @click="saveSuggest">保存建议</Button>
          <!--<Button type="info" @click="showList = true">任务列表 {{solvedCount}}/{{errorCount}} </Button>-->
        </div>
        <div class="ai-result" style="display: inline-block">
          <Button type="default" :loading="nextLoading" :disabled="errorCount === 0" @click="nextResult" shape="circle" icon="md-arrow-forward"></Button>
        </div>
        </Col>
      </Row>
      <Row>
        <Col span="24">
        <Card :dis-hover="true" v-if="this.result ? this.result : ''">
          <h3 v-for="t in (this.result ? this.result : '').split('\n')">
            <div v-if="t.indexOf('问题点:') >= 0 " style="color:red">{{t}}</div>
            <div v-else-if="t.indexOf('建议:') >= 0 " style="color:red">{{t}}</div>
            <div v-else-if="t.indexOf('加入hint:') >= 0 " style="color:red">{{t}}</div>
            <div v-else>{{t}}</div>
            <!--<div v-if="t.join('').indexOf('问题点')">{{t}}</div>-->
          </h3>
        </Card>
        </Col>
      </Row>
      <Divider />
      <!--<Row>-->
        <!--<Col span="12">-->
          <!--<h3>SQL内容</h3>-->
        <!--</Col>-->
        <!--<Col span="12" class="align-right">-->
          <!--<div style="display:inline-block;cursor:pointer;margin-right:10px;"><h3>AI建议</h3></div>-->
          <!--<div style="display:inline-block"><h3>DBA建议</h3></div>-->
        <!--</Col>-->
      <!--</Row>-->
      <Row>
        <Col span="24">
          <Tabs :animated="false"  @on-click="initEditor">
            <TabPane label="AI评审建议" icon="md-desktop">
              <div style="padding:5px 0">
                <div style="display:inline-block;">
                  <h3>SQL信息</h3>
                </div>
                <div style="float:right;">
                  <h3>AI建议</h3>
                </div>
              </div>
              <div id="view"></div>
            </TabPane>
            <TabPane label="DBA评审建议" icon="md-person" v-if="rowInfo.sql_review_suggest || isAdmin">
              <textarea id="view-person" v-model="rowInfo.sql_review_suggest" style="width:100%;height:300px;font-size: 14px"></textarea>
            </TabPane>
          </Tabs>
        </Col>
      </Row>
      <div slot="footer">
        <Button @click="show = false" type="default">关闭</Button>
      </div>
    </Modal>
    <Drawer title="任务列表" :mask-closable="true" :closable="true" v-model="showList" width="500" :mask="true">
      <task-sql-detail-list ref="taskSqlDetailList" :search="aiResult" @showErrorCount="showErrorNumber" @selectRow="changeSelect">
      </task-sql-detail-list>
    </Drawer>
    <!--<Modal v-model="showList" width="800" title="变更点列表">-->
      <!--<div slot="footer">-->
        <!--<Button @click="showList = false" type="default">关闭</Button>-->
      <!--</div>-->
    <!--</Modal>-->
  </div>
</template>
<script>
  // import axios from 'axios';
  // import util from '../../libs/util'


  import "codemirror/theme/ambiance.css";
  import "codemirror/lib/codemirror.css";
  import "codemirror/addon/hint/show-hint.css";

  let CodeMirror = require("codemirror/lib/codemirror");
  require("codemirror/addon/edit/matchbrackets");
  require("codemirror/addon/selection/active-line");
  require("codemirror/mode/sql/sql");
  require("codemirror/addon/hint/show-hint");
  require("codemirror/addon/hint/sql-hint");

  import { getTaskSqlDetail, updateTaskSqlList, getTaskSqlList } from '@/api/task';
  import TaskSqlDetailList from './taskSqlDetailList'

  export default {
    name: 'TaskSqlDetail',
    components: {
      TaskSqlDetailList
    },
    props: {
      aiResult: {type: Object, default: () => {}},
      reviewCount: {type: Number, default: () => 0}
    },
    data () {
      return {
        prevLoading: false,
        nextLoading: false,
        show: false,
        showList: false,
        sequence: '',
        // detail: {},
        result: '',
        updateLoading: false,
        rowInfo: {},
        errorCount: 0,
        solvedCount: 0,
        review_status: undefined,
        aiEditor: undefined,
        dbaEditor: undefined,
      };
    },
    computed: {
      isAdmin() {
        return sessionStorage.getItem('auth').indexOf('admin') >= 0
      }
    },
    methods: {
      updateTaskSql(v) {
        this.$Modal.confirm({
          title: `确认人工审核 ${v === '1'? '通过' : '不通过'}`,
          'z-index': 1010,
          // content: '请确认创建Review任务',
          onOk: () => {
            this.updateLoading = true
            updateTaskSqlList({id: this.rowInfo.id, review_status: v, review_mark: 1, updated_by: sessionStorage.getItem('user')}).then(res => {
              if (res.data.status === 'success') {
                this.$Message.success({
                  content: '信息已保存',
                  duration: 5,
                  closable: true
                });
                this.rowInfo.review_status = v
                this.$refs['taskSqlDetailList'].getData()
                this.$emit('reload', true)
                // this.getData()
              } else {
                this.$Message.error({
                  content: '保存信息错误',
                  duration: 5,
                  closable: true
                });
              }
            }).catch(e => {
              console.log(e)
            }).finally(() => {
              this.updateLoading = false
            })
          },
          onCancel: () => {
            // this.$Message.info('Clicked cancel');
          }
        });
      },
      resize(mergeView) {
        let height = 600
        for (; ;) {
          if (mergeView.leftOriginal())
            mergeView.leftOriginal().setSize(null, height);
          mergeView.editor().setSize(null, height);
          if (mergeView.rightOriginal())
            mergeView.rightOriginal().setSize(null, height);

          const newHeight = 700;
          if (newHeight >= height) break;
          else height = newHeight;
        }
        mergeView.wrap.style.height = height + "px";
      },
      saveSuggest() {
        console.log(this.rowInfo.sql_review_suggest)
        // console.log(this.dbaEditor)
        // console.log(this.dbaEditor.editor().getValue())

        this.$Modal.confirm({
          title: `确定要保存建议吗？`,
          'z-index': 1010,
          // content: '请确认创建Review任务',
          onOk: () => {
            this.updateLoading = true
            updateTaskSqlList({id: this.rowInfo.id, sql_review_suggest: this.rowInfo.sql_review_suggest}).then(res => {
              if (res.data.status === 'success') {
                this.$Message.success({
                  content: '信息已保存',
                  duration: 5,
                  closable: true
                });
                this.$emit('reload')
                this.getData()
              } else {
                this.$Message.error({
                  content: '保存信息错误',
                  duration: 5,
                  closable: true
                });
              }
            }).catch(e => {
              console.log(e)
            }).finally(() => {
              this.updateLoading = false
            })
          },
          onCancel: () => {
            // this.$Message.info('Clicked cancel');
          }
        });

      },
      initEditor() {
        setTimeout(() => {
          this.initAIEditor(this.rowInfo)
          // this.initDBAEditor(this.rowInfo)
        }, 50)
      },
      initAIEditor(data) {
        orig1 = "";
        value = '[执行计划]\n' + data.plan_text + '\n[sql文本]\n' + data.sql_text + '\n\n\n\n';
        orig2 = ''
        var value, orig1, orig2, dv, panes = 2, highlight = true, connect = "align", collapse = false;
        var target = document.getElementById("view");
        target.innerHTML = "";
        dv = CodeMirror.MergeView(target, {
          value: value,
          origLeft: panes === 3 ? orig1 : null,
          orig: orig2,
          origRight: orig2,
          lineNumbers: true,
          mode: "text/x-sql",
          highlightDifferences: highlight,
          smartIndent: true,
          // lineWrapping: true,
          connect: true,
          collapseIdentical: false,
          revertButtons: true,
          readOnly: true
        });
        this.aiEditor = dv
        this.resize(dv)
      },
      // initDBAEditor(data) {
      //   console.log(data)
      //   orig1 = data.sql_review_suggest ? data.sql_review_suggest : '无建议'
      //   value = '[执行计划]\n' + data.plan_text + '\n[sql文本]\n' + data.sql_text + '\n\n\n\n';
      //   // orig2 = data.sql_review_suggest ? data.sql_review_suggest : ''
      //   var value, orig1, orig2, dv, panes = 2, highlight = true, connect = "align", collapse = false;
      //   if(this.dbaEditor !== undefined) {
      //     this.dbaEditor.setValue('[暂无建议]\n')
      //     return true
      //   }
      //   var target = document.getElementById("view-person");
      //   dv = CodeMirror.fromTextArea(target, {
      //     value: orig1,
      //     // origLeft: 1,
      //     // orig: orig2,
      //     lineNumbers: true,
      //     mode: "text/x-sql",
      //     highlightDifferences: highlight,
      //     smartIndent: true,
      //     // lineWrapping: true,
      //     connect: true,
      //     collapseIdentical: false,
      //     revertButtons: true,
      //   });
      //   this.dbaEditor = dv
      //   this.dbaEditor.setSize('auto','500px');
      //   // this.resize(dv)
      // },
      prevResult() {
        this.prevLoading = true
        this.nextLoading = true
        this.$refs['taskSqlDetailList'].prevResult(this.rowInfo.sql_sequence)
      },
      nextResult() {
        this.prevLoading = true
        this.nextLoading = true
        this.$refs['taskSqlDetailList'].nextResult(this.rowInfo.sql_sequence)
      },
      showErrorNumber(row) {
        console.log(row)
        row.data.forEach(v => {
          if (v.sql_sequence === this.rowInfo.sql_sequence) {
            this.rowInfo.review_status = v.review_status
          }
        })
        this.errorCount = row.count;
        this.solvedCount = row.count- row.review_count;
        // this.solvedCount = row.data.filter(v => v.ai_result === 'NOPASS' && v.review_status).length
      },
      getData() {
        getTaskSqlList(15, 0, '', '', '', '', this.rowInfo.sql_sequence).then(res => {
          if(res.data.data.length !== 0) {
            console.log(res.data.data)
            this.rowInfo = res.data.data[0]
            this.result = this.rowInfo.ai_recommend
            setTimeout(() => {
              this.initAIEditor(this.rowInfo)
              // this.initDBAEditor(this.rowInfo)
            }, 50)
          }
        }).catch(e => {
        })
      },
      changeSelect(row) {
        console.log(row)
        this.rowInfo = row
        this.review_status = row.review_status
        this.result = row.ai_recommend
        setTimeout(() => {
          this.initAIEditor(row)
          // this.initDBAEditor(row)
        }, 50)

      },
      async showDetail(row) {
        console.log(row)
        this.rowInfo = row
        this.show = true
        this.result = row.ai_recommend
        setTimeout(() => {
          this.initEditor()
          this.initEditor()
        }, 50)
        // await getTaskSqlDetail(15, 0, row.sql_sequence).then(res => {
        //   if(res.data.data.length !== 0) {
        //     console.log(res.data.data)
        //     this.detail = res.data.data[0]
        //     this.result = JSON.parse(this.detail.ai_result)
        //     this.initAIEditor(res.data.data[0])
        //   }
        // }).catch(e => {
        //   this.bus.$emit('errorHandler', e);
        // }).finally(() => {
        //
        // })
        this.$refs['taskSqlDetailList'].getData()
      }
    },
    mounted () {
      this.bus.$on('closePrevNextLoading', (v) => {
        this.prevLoading = v
        this.nextLoading = v
      })
    },
    destroyed() {
      this.bus.$off('closePrevNextLoading')
    }
  };
</script>
